import { Chip } from 'material-ui';
import React, {useEffect, useLayoutEffect} from 'react';
import styles from './Emotions.css';
import EmotionChip from '../containers/EmotionChip';
import Footer from '../components/Footer';
import emotions from '../data/emotions.json';

const Emotions = () => {
    useLayoutEffect(() => {        
        document.body.className='emotions';
    });
    
    return(
    <div className="emotions-container">
        <h3>How Do You Feel?</h3>
        {emotions.map((i,k)=>{ return <EmotionChip label={i.title} icon={i.icon} iconFirst={false} url={`/${i.title.toLowerCase()}`} key={k}/>})}
    </div>);
}
export default Emotions;