import React from 'react';
import ReactDOM from 'react-dom';
import {BrowserRouter as Router,Switch,Route} from "react-router-dom";
import { Transition, TransitionGroup } from 'react-transition-group';
import {play, exit} from './utils/timelines';
import backgrounds from './data/backgrounds.json';

import './index.css';
import Emotions from './components/Emotions';
import Emotion from './components/Emotion';

/* Random background selection for selected emotion */
const backgroundNum = Math.floor(Math.random() * backgrounds.length);
const backgroundObj = backgrounds[backgroundNum];

ReactDOM.render(
      <Router>
          <Route render={({location})=>{
            const { pathname, key } = location
            return(
              <TransitionGroup component={null}>
                <Transition
                  key={key}
                  appear={true}
                  onEnter={(node, appears) => play(pathname, node, appears)}
                  onExit={(node, appears) => exit(node, appears)}
                  timeout={{enter: 750, exit: 0}}
                >
                  <Switch location={location}>
                    <Route exact path="/"><Emotions/></Route>
                    <Route path="/:emotion"><Emotion backgroundObj={backgroundObj}/></Route>
                  </Switch>
                </Transition>                
              </TransitionGroup>
            )
          }}/>
      </Router>,
  document.getElementById('root')
);