import React, {useEffect} from 'react';
import {useParams} from "react-router-dom";
import styles from './Emotion.css';
import EmotionChip from '../containers/EmotionChip';
import emotions from '../data/emotions.json';
import backgrounds from '../data/backgrounds.json';


const Emotion = () => {
    let {emotion} = useParams();
    emotion = emotion.toLowerCase();
    
    const emotionObj = emotions.find(i=>i.title.toLowerCase()==emotion);
    
    
    useEffect(() => {      
        if(!emotionObj)  
            return;

        const backgroundNum = Math.floor(Math.random() * backgrounds.length);
        const backgroundObj = backgrounds[backgroundNum];

        document.body.className = `emotion ${backgroundObj.light?'light':'dark'}`;
        document.body.style.backgroundColor = backgroundObj.color;

        return () => {
            document.body.className = ``;
            document.body.style.backgroundColor = null;
        };

    },[emotionObj]);

    return(
    <div className="emotion-container">
        <div className="title">
            <h2>It's totally</h2><h1>OK</h1><h3>to feel {emotionObj.title.toLowerCase()} <i>{emotionObj.icon}</i></h3>
        </div>
        <div className="links">
            {!emotionObj.positive && <EmotionChip label={"Call Local Helpline"} icon={"📞"} iconFirst={true} />}
            {emotionObj.links.map((i,k)=>{ return i.title ? <EmotionChip label={i.title} icon={i.icon} iconFirst={true} url={i.url} key={k}/> : null})}            
            <EmotionChip cssClass={"share"} label={"Share"} icon={"↗️"} iconFirst={true} />
        </div>
    </div>);
}
export default Emotion;